$col-background: rgb(243, 243, 243);
@mixin box-gradient {
    background: rgba(236,49,35,1);
    background: -moz-linear-gradient(-45deg, rgba(236,49,35,1) 0%, rgba(236,49,35,1) 49%, rgba(138,35,29,1) 100%);
    background: -webkit-gradient(left top, right bottom, color-stop(0%, rgba(236,49,35,1)), color-stop(49%, rgba(236,49,35,1)), color-stop(100%, rgba(138,35,29,1)));
    background: -webkit-linear-gradient(-45deg, rgba(236,49,35,1) 0%, rgba(236,49,35,1) 49%, rgba(138,35,29,1) 100%);
    background: -o-linear-gradient(-45deg, rgba(236,49,35,1) 0%, rgba(236,49,35,1) 49%, rgba(138,35,29,1) 100%);
    background: -ms-linear-gradient(-45deg, rgba(236,49,35,1) 0%, rgba(236,49,35,1) 49%, rgba(138,35,29,1) 100%);
    background: linear-gradient(135deg, rgba(236,49,35,1) 0%, rgba(236,49,35,1) 49%, rgba(138,35,29,1) 100%);
filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ec3123', endColorstr='#8a231d', GradientType=1 );
}
